import { HttpErrorResponse } from '@angular/common/http';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';

import { AngularTokenService } from 'angular-token';

import icVisibility from '@iconify/icons-ic/twotone-visibility';
import icVisibilityOff from '@iconify/icons-ic/twotone-visibility-off';

import { fadeInUp400ms } from '@vex/animations/fade-in-up.animation';

import { OmniauthCallbacksApi } from '@core/http/omniauth-callbacks-api.service';
import { environment } from '@environments/environment';

@Component({
  selector: 'vex-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [
    fadeInUp400ms,
  ],
})
export class LoginComponent implements OnInit {
  form: FormGroup;

  inputType = 'password';
  visible = false;

  icVisibility = icVisibility;
  icVisibilityOff = icVisibilityOff;

  constructor(private router: Router,
              private fb: FormBuilder,
              private cd: ChangeDetectorRef,
              private snackbar: MatSnackBar,
              private tokenService: AngularTokenService,
              private omniauthCallbacksApi: OmniauthCallbacksApi
  ) {}

  ngOnInit() {
    this.form = this.fb.group({
      email: ['', Validators.required],
      password: ['', Validators.required],
    });
  }

  loginWithGoogle(): void {
    this.tokenService.tokenOptions.oAuthBase = environment.apiUrl;
    // this.tokenService.tokenOptions.oAuthWindowType = 'sameWindow';
    this.tokenService.signInOAuth(
      'google_oauth2'
    ).subscribe(
      res => {
        this.router.navigate(['/']);
      },
      error => {

      }
    );
  }

  login() {
    const form = this.form.value;

    this.tokenService.signIn({
      login:    form.email,
      password: form.password,
    }).subscribe(
      res => {
        this.router.navigate(['/']);
      },
      (error: HttpErrorResponse) => {
        if (error.status === 401) {
          this.snackbar.open('Incorrect username or password', 'close', {
            duration: 2000,
          });
        } else {
          this.snackbar.open('An unexpected error occurred, please try again later', 'close', {
            duration: 2000,
          });
        }
      }
    );
  }

  toggleVisibility() {
    if (this.visible) {
      this.inputType = 'password';
      this.visible = false;
      this.cd.markForCheck();
    } else {
      this.inputType = 'text';
      this.visible = true;
      this.cd.markForCheck();
    }
  }
}
